.page-header-navigation {
  position: relative;
  min-width: 320px;
}

//  
.page-header__logo {
  position: absolute;
  left: 50%;
  margin-left: -92.5px;
}

.page-header__logo-image {
  height: 96px;
}

@media (min-width: $mobile-width) {

  //    
  //    
  .page-header-navigation:focus-within .burger-icon {
    outline: solid 1px #000000;
  }

  .page-header {
    background-color: #ffffff;
    width: 320px;
    height: 56px;
  }

  //      
  .page-header-navigation__burger-menu-label {
    width: 60px;
    height: 56px;
    position: absolute;
    right: 0;
    z-index: 1;
  }

  .burger-icon {
    position: absolute;
    right: 0;
    padding: 17px 20px;
  }

  // C    
  .burger-icon:hover {
    opacity: 0.6;
  }

  //    
  .burger-icon:active {
    opacity: 0.3;
  }

  //    ,  ,  
  //  
  .page-header-navigation__burger-menu-checkbox:checked ~ .page-header-navigation__burger-menu-label .burger-icon {
    display: none;
  }

  //  
  .page-header-navigation__burger-menu-checkbox:checked ~ .page-header-navigation__burger-menu-label::before {
    position: absolute;
    top: 0;
    right: 0;
    content: url("../img/burger-cross-icon.svg");
    width: 22.2px;
    height: 22.2px;
    padding: 17px 20px;
  }

  //     
  .page-header-navigation__burger-menu-checkbox:checked ~ .page-header-navigation__burger-menu-label:hover {
    opacity: 0.6;
  }

  //    
  .page-header-navigation__burger-menu-checkbox:checked ~ .page-header-navigation__burger-menu-label:active {
    opacity: 0.3;
  }

  //  
  .page-header-navigation__burger-menu-checkbox:checked ~ .page-header__menu-list {
    display: flex;
  }

  .page-header__menu-list {
    position: absolute;
    left: 0;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #81b3d2;
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .page-header__menu-item {
    display: flex;
    min-width: 320px;
    justify-content: center;
    align-items: center;
    height: 55px;
    margin-bottom: 1px;
    box-shadow: 0 1px 0 #01010133, inset 0 1px 0 #ffffff33;
  }

  //     
  .page-header__menu-item:last-child::after {
    position: absolute;
    bottom: 0;
    content: "";
    min-width: 320px;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }

  .page-header__menu-link {
    text-decoration: none;
    color: #ffffff;
    font-family: "PT Sans", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
  }
}

//   
@media (min-width: $tablet-width) {

  //     
  .page-header__menu-item:last-child::after {
    display: none;
  }

  .page-header-navigation {
    position: static;
  }

  .page-header__menu-list {
    position: static;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    background-color: #ffffff;
  }

  .page-header__menu-item {
    min-width: 153px;
    height: 56px;
    box-shadow: none;
  }

  .page-header__menu-item:nth-child(2) {
    margin-right: 153px;
  }

  .page-header__menu-link {
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    padding: 15px;
  }

  //     
  .page-header__menu-link:hover {
    color: #81b3d2;
  }

  //     
  .page-header__menu-link:active {
    color: #000000;
    opacity: 0.3;
  }

  .page-header__menu-link--active {
    color: #766357;
  }

  .page-header-navigation__burger-menu-label {
    display: none;
  }
}

//   
@media (min-width: $desktop-width) {
  .page-header__menu-item {
    width: 240px;
    height: 56px;
    box-shadow: none;
  }

  .page-header__menu-item:nth-child(2) {
    margin-right: 240px;
  }
}
